import helper from '../../common/helper.js';
export default {
	data() {
		return {
			Newpassword:'',
			Oldpassword:'',
			conpassword:'',
			userdata:''
		};
	},
	methods: {
		formSubmit: function(e) {
			if (this.Oldpassword != this.Oldpassword ) {
				uni.showToast({
					icon: 'none',
					title: '请输入旧密码'
				});
				return;
			}
			if (this.Newpassword.length < 6 || this.Newpassword.length > 12 || this.Oldpassword.length < 6 || this.Oldpassword.length > 12) {
				uni.showToast({
					icon: 'none',
					title: '请输入6-12个字符的密码'
				});
				return;
			}
			if (this.Newpassword != this.conpassword ) {
				uni.showToast({
					icon: 'none',
					title: '密码确认错误'
				});
				return;
			}
			this.userdata = e.detail.value
			uni.request({
				url:helper.websiteUrl+'usercenter/updatepw' ,
				method: 'GET',
				data:helper.postdata({
					"userid": helper.getstate().userid,
					"Newpassword": this.userdata.Newpassword,
					"Oldpassword": this.userdata.Oldpassword
				}),
				success: res => {
					if (res.data.code == 200) {
						uni.showToast({
							icon: 'none',
							title: '修改成功,请重新登录。',
							duration:3000,
						});
						helper.logout();
					} else {
						uni.showToast({
							icon: 'none',
							title: res.data.info
						});
					}
				},
				fail: () => {
					uni.showToast({
						icon: 'none',
						title: '网络异常,请稍后重试'
					});
				}
			});
		}
	}
}
